body {
  margin: 0;
  padding: 0;
  font-family:'Courier New', Courier, monospace;
}

.App {
  display: grid;
  grid-template-columns: 1fr 1fr;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.left {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.right {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: #f3f6ff;
  height: 100vh;
  gap: 50px;
}

.welcome {
  text-align: center;
}

button {
  width: 450px;
  height: 55px;
  border-radius: 5px;
  font-size: 16px;
  border: 2px solid lightgrey;
  background-color: white;
}

button:hover {
  background-color: #00dcf93f;
  border-color: #00DCF9;
}

.options button:focus {
  background-color: #00dcf93f;
  border-color: #00DCF9;
}

.options {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 15px;
}

.buttons {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 60px;
  gap: 170px;
}

.buttons button {
  width: 140px;
  height: 40px;
  font-size: 15px;
  background-color: #F3F4F6;
}



h1 {
  text-align: center;
}





